let Swiper = require('../../../bower_components/swiper/dist/js/swiper.min');
let $      = jQuery;


function options (index, _name, _preview, _pagination){
  let options  = {};
  let setting  = {};
  let _default = {
      // Optional parameters
      //direction: 'vertical',
      loop: true,
      // If we need pagination
      pagination: {
        el: '.swiper-pagination-' + index,
        clickable :true,
        type: (_pagination && _pagination == 'custom') ? 'custom' : 'bullets',
        renderCustom: function(swiper, current, total) {
          if(_pagination && _pagination == 'custom'){
            var display = [];

            _.each(swiper.slides, function(item, index) {
              display.push(item.dataset.pagination);
            });
            var text = '';
            for (let i = 1; i <= total; i++) {
              if (current != i) {
                text += "<span class='swiper-pagination-custom-item' data-index='" + i + "'>" + display[i] + "</span>";
              } else {
                text += "<span class='swiper-pagination-custom-item-active' data-index='" + i + "'>" + display[i] + "</span>";
              }
            }
            return text;

          }else{

            return false;

          }

        }
      },
      // Navigation arrows
      navigation: {
        nextEl: '.swiper-button-next-' + index,
        prevEl: '.swiper-button-prev-' + index
      },
      // And if we need scrollbar
      scrollbar: {
        el: '.swiper-scrollbar-' + index
      }
  }
  setting = Drupal.behaviors.swiperSettings
            && Drupal.behaviors.swiperSettings[_name];

  if(setting && !_.isNumber(_name)){
    options = _.extend(_default, setting);
  }else{
    options = _default;
  }
  if(_preview){
    options.slidesPerView = _preview;
  }


  return options;
}

module.exports = function(myswiper){
  Drupal.swiper = [];

  $(myswiper).on('click', 'span.swiper-pagination-custom-item', function(e){
      e.preventDefault();

      var clicked = $(this);
      var _target = clicked.data('index');
      var node    = clicked.parents('.swiper-container').data('node');

      if(!clicked.hasClass('.swiper-pagination-custom-item-active')){
        Drupal.swiper[node].slideTo(_target);
      }

  });

  $(myswiper).each(function(index,el){
    let $this       = $(el);
    let _name       = $this.data('name') || index;
    let _preview    = $this.data('preview');
    let _pagination = $this.data('pagination');

    $this.addClass('swiper_base_' + index);

    if($this.parent().is('.swiper-wrapper')){
      let $parent = $this.parent();
      $parent.find('.swiper-button-next').addClass('swiper-button-next-' + index);
      $parent.find('.swiper-button-prev').addClass('swiper-button-prev-' + index);
    }else{
      $this.find('.swiper-button-next').addClass('swiper-button-next-' + index);
      $this.find('.swiper-button-prev').addClass('swiper-button-prev-' + index);
    }
    $this.find('.swiper-scrollbar').addClass('swiper-scrollbar-' + index);
    $this.find('.swiper-pagination').addClass('swiper-pagination-' + index);

    let node = _.isNumber(_name) ? _name : (_name + '_' + index);
    $this.attr('data-node', node);
    Drupal.swiper[node] = new Swiper('.swiper_base_' + index, options(index, _name, _preview, _pagination));

  });
}