let Drupal = window.Drupal || {};

(function ($) {

  Drupal.behaviors.siteScript = {
    attach (context) {

      $('#content-modal-nav').modal({
        backdrop: false,
        'show': true,
      });

      // Scroll top button
      this.librarys.scrolltop($, 600);

      // Message alert button
      this.librarys.messages;

      // Default Set with drupal.js
      // this.librarys.footer($);

      // Swiper
      this.plugins.swiper('.swiper-container');

    },
    plugins:{
      'swiper' : require('./plugins/swiper')
    },
    librarys:{
      // Core
      'messages' : require('./drupal/wenui.drupal.messages'),
      'footer'   : require('./drupal/wenui.drupal.footer'),
      'scrolltop': require('./drupal/wenui.drupal.scrolltop')
    }
  }

}(jQuery));
