module.exports = function($,speed){

	let $btn_top      = $(".btn-top");
	let $btn_top_wrap = $btn_top.parents('.footer');

	if($btn_top && !$btn_top_wrap.length){
		$btn_top.hide();

		$(window).scroll(function () {
			if ($(this).scrollTop() > 100) {
				$btn_top.fadeIn();
			} else {
				$btn_top.fadeOut();
			}
		});
	}

	$btn_top.bind('click', function(event) {
		$('body,html').animate({
			scrollTop: 0
		}, speed);
		return false;
	});

}